.addSection {
  position: relative;
  padding: 10px 0 50px;
  text-align: center;
  background-image: linear-gradient(rgb(22, 22, 22), rgb(19, 19, 19));
}

.addSection h2 {
  padding: 20px;
  margin-top: 40px;
  font-size: 30px;
  color: #ffffff;
}

form {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 70%;
  margin: auto;
  background-image: linear-gradient(rgb(179, 80, 41), #e6541a);
  border-radius: 5px;
}

.inputSection {
  display: flex;
  flex-direction: row;
}

.formCol {
  width: 40%;
  margin: 30px 50px;
}

.formCol label {
  display: flex;
  justify-content: start;
  margin: 12px;
  font-size: 18px;
  font-weight: 500;
  text-align: start;
  color: #ffffff;
}

.formCol input, .formCol select {
  height: 28px;
  width: 100%;
  border: 1px solid rgb(146, 146, 146);
  border-radius: 5px;
}

.submitButton {
  padding: 10px 30px;
  margin: 5px 0 30px;
  font-size: 18px;
  text-transform: uppercase;
  color: #ffffff;
  background-image: linear-gradient(rgb(14, 136, 14), rgb(8, 107, 8));
  border: 1px solid rgb(0, 75, 0);
  border-radius: 3px;
}
