.tbody td {
  position: relative;
  height: 40px;
  text-align: center;
  left: 5px;
  border-bottom: 1px solid rgb(192, 192, 192);
}

td {
  position: relative;
  text-align: start;
}

.edit {
  padding: 8px 14px;
  color: #ffffff;
  background-color: #e6541a;
  border: 1px solid #ffffff;
  border-radius: 3px;
}

.delete {
  padding: 8px 14px;
  color: #ffffff;
  background-color: #a81515;
  border: 1px solid #ffffff;
  border-radius: 3px;
}
