.title {
    align-items: center;
    background-color:#3D4855;
    border-bottom: 1px grey solid;
    color: #fff;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 500;
    height: 75px;
    margin: 0px;
    text-align: center;
}
.title h2 {
    margin: 0;
    font-size: 20px;;
}
.aside {
    background-color:#232F3E;
    width: 20%;
}
.nav {
    display: flex;
    flex-direction: column;
    height: 100vh;
}
ul {
    margin: 0;
    padding-top: 30px;
    list-style-type: none;
}
li {
    padding-left: 10px;
    text-align: start;
    margin-bottom: 20px;
}
a {
    color: #ffffff;
    text-decoration: none;
}
a:hover {
    color: #FC6225;
}
img {
    margin-right: 15px;
}