.header {
    align-items: center;
    background-color: #FC6225;
    border-bottom: 1px grey solid;
    color: #ffffff;
    display: flex;
    font-size: 20px;
    height: 75px;
    justify-content: space-between;
    margin: 0;
    width: 100%;
}
h1 {
    margin: 0;
    padding-left: 30px;
}
nav {
    margin-right: 20px;
}

nav ul {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
}
nav ul li {
    margin: 0;
    padding: 0;
}
.logIn:hover {
    color: black;
}