.table {
  display: flex;
  justify-content: center;
}

.table table {
  position: relative;
  padding: 40px;
  margin: 30px 0;
  width: 98%;
  border: 1px solid rgb(241, 241, 241);
  border-radius: 4px;
  box-shadow: 4px 4px 10px rgb(153, 153, 153);
}

.table th {
  padding-bottom: 15px;
  color: #e6541a;
  font-size: 20px;
}
